<template>
  <div class="main">
    <swiper :options="swiperOption" class="main">
      <swiper-slide>
        <img id="avater" src="./assets/images/avater.jpg">
        <h1>周厚杰</h1>
        <p class="text">把生活活成段子 · 把代码写成诗</p>
        <h4>web前端工程狮</h4>
        <p class="text">Email：
          <a class="linkTip" href="mailto:541526968@qq.com" target="_blank">541526968@qq.com</a>
        </p>
      </swiper-slide>
      <swiper-slide>
        <h1>关键词</h1>
        <div class="tags ani" swiper-animate-effect="animated fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">中大型 · web应用系统 项目经验</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">两年开发经验</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">掌握less、bootstrap，熟悉html5/css3、flex布局</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">掌握jquery、原生js编程，熟悉Vue等mvvm框架</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">掌握Ajax、Json</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">jqGrid、layerUI、Highcharts、Swiper等插件使用经验。</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">移动端web开发经验</P>
        </div>
        <div class="tags ani" swiper-animate-effect="fadeInUp" swiper-animate-duration="0.5s">
          <P class="tags_p">了解JAVA、C#、SQL、mongoDB</P>
        </div>
      </swiper-slide>
      <swiper-slide>Slide 3</swiper-slide>
      <swiper-slide>Slide 4</swiper-slide>
      <swiper-slide>Slide 5</swiper-slide>
      <swiper-slide>Slide 6</swiper-slide>
      <swiper-slide>Slide 7</swiper-slide>
      <swiper-slide>Slide 8</swiper-slide>
      <swiper-slide>Slide 9</swiper-slide>
      <swiper-slide>Slide 10</swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          direction: 'vertical',
          mousewheelControl: true
        }
      }
    }
  }

</script>

<style>
  html,
  body,
  .main {
    width: 100%;
    height: 100%;
  }

  html,
  body {
    background: #eee;
    font-family: Lato, "Microsoft YaHei", sans-serif;
    margin: 0;
    padding: 0;
    color: #eef3f0;
  }

  .text {
    color: #bcc0cc;
  }

  h1 {
    font-weight: 500;
    font-size: 7vmin;
    text-shadow: .1vmin .2vmin #272838, .2vmin .4vmin #e7e7e8;
    margin-bottom: 0;
    margin-top: 0;
  }

  h4 {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: bold;
  }

  .linkTip {
    color: #953750;
    transition: 0.5s;
    text-decoration: none;
  }

  .linkTip:hover {
    color: #fc425f;
  }

  .swiper-slide {
    text-align: center;
    font-size: 1.8vmin;
    background: #272838;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  /*首页*/

  #avater {
    width: 20vmin;
    height: 20vmin;
    border: 1vmin solid #2c2c41;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    box-shadow: 0 0 .3vmin .3vmin rgba(0, 0, 0, .3);
    overflow: hidden;
    margin-bottom: 3vmin;
  }
  /*关键词*/
  .tags {
    color: #bcc0cc;
    background-color: #2c2c41;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -o-border-radius: 7px;
    border-radius: 7px;
    margin-bottom: 10px;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .3);
  }

  .tags_p {
    margin: 7px 15px;
    transition: 0.5s;
  }

  .tags_p:hover {
    color: #fc425f;
    cursor: default;
  }

</style>



